/*
* Breakpoints 
*/
@media screen and (min-width: 768px) {
  .gallery {
    position: relative;
    display: block;
    width: 36%;
    height: 100%;
    overflow-y: auto; } }

.gallery-figure {
  margin: 0 0 1em; }
  .gallery-figure:nth-last-child(1) {
    margin-bottom: 0; }
  .gallery-figure > img {
    display: block;
    transition: all .5s ease-in; }
    .is-inverted .gallery-figure > img {
      filter: invert(0.8); }
