.nav {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  background-color: #222;
  color: #fefefe;
  padding: 1.75em;
  transition: color .3s ease-in, background-color .3s ease-in; }
  @media screen and (min-width: 768px) {
    .nav {
      background-color: transparent;
      color: #222; }
      .is-inverted .nav {
        color: #fefefe; } }
  .nav a::after {
    background-color: #fefefe; }
  @media screen and (min-width: 768px) {
    .nav a::after {
      background-color: #222; } }

.name {
  margin: 0;
  font-size: 1em;
  font-weight: 400; }
